.slider-video {
  .owl-nav{
    position: absolute;
    top: -90px;
    right: 0;
    margin-top: 0;
    line-height: 50px;
    vertical-align: middle;
    height: 50px;
    border-radius: 50%;
    .owl-prev,
    .owl-next{
      margin: 0;
      .our-video--next,
      .nav--previous{
        background-color: #dfdfdf;
        margin-left: 15px;
      }
    }
  }
  .item{
    &__video{
      iframe{
        width: 100%;
        height: 506px;
      }
    }
    &__detail{
      padding: 25px;
      position: relative;
      background-color: #fff;
      font-family: 'opensans',sans-serif;
      font-weight: 400;
      max-height: 506px;
      box-shadow: 0 1.5px 2px rgba(0, 0, 0, 0.16), 0 0px 0px rgba(0, 0, 0, 0.23);
      &--infor{
        .rooms{
          margin: 20px 0 15px;
          color: #222;
          span{
            display: inline-block;
            margin-right: 25px;
          }
        }
        .name{
          font-size: 20px;
          color: #222;
          margin:30px 0 10px;
          font-family: 'poppins',sans-serif;
          font-weight: 400;
        }
        .address{
          color: #666;
        }
        .number{
          font-size: 20px;
          color: #222;
          margin-bottom: 10px;
        }
        .price{
          font-size: 20px;
          color: #ea723d;
          font-family: 'poppins',sans-serif;
        }
        .name-room{
          text-transform: uppercase;
          font-size: 12px;
        }
        a.btn-play{
          position: absolute;
          right: 25px;
          bottom: 25px;
          height: 40px;
          line-height: 40px;
          width: 93px;
          vertical-align: middle;
          text-align: center;
          border-radius: 35px;
          font-family: 'opensans',sans-serif;
          font-weight: 400;
          font-size: 18px;
          text-transform: uppercase;
          color: #fff;
        }
      }
    }
  }
  .item-mobile{
    width: 100%;
    padding: 0 15px;
    .item__detail{
      max-height: 1000px;
    }
    iframe{
      width: 100%;
      height: 300px;
    }
  }
}
