.footer {
  &__top {
    background-color: #222222;
    padding: 85px 0 64px;
  }
  &__contact{
    padding-left: 70px;
  }
  &__menu{
    margin-left: 160px;
    margin-right: 50px;
  }
  &__bottom {
    background-color: #111;
    height: 50px;
    line-height: 50px;
    vertical-align: middle;
    font-weight: 400;
    color: #555;
    p.design-by{
      display: inline-block;
      padding-left: 20px;
    }
    a.login{
      display: inline-block; 
      padding-right: 20px;
      position: relative;
      &:after{
        content: '|';
        position: absolute;
        right: -2px;
      }
    }
  }

  &__wrap {
    width: 100%;
  }

  &__col {
    padding: 0 15px;

    &--title {
      color: #fff;
      
      font-size: 20px;
      height: 34px;
      line-height: 34px;
      vertical-align: bottom;
      position: relative;
      margin-bottom: 22px;

      a {
        display: block;
      }

      img {
        display: block;
      }

      h3 {
        font-size: 20px;
        line-height: 40px;
        font-weight: 600;
      }
    }

    &--text {
      color: #666;
      font-family: 'opensans', sans-serif;
      p {
        line-height: 25px;
        
        font-weight: 400;
      }
    }
  }

  .link-society {
    margin-top: 29px;
    font-size: 0;
    li {
      color: #fff;
      margin-right: 25px;
      font-size: 14px;
      a {
        display: block;
        height: 40px;
        width: 40px;
        text-align: center;
        line-height: 40px;
        vertical-align: middle;
        background-color: #393939;
        border-radius: 50%;
        transition: all .3s;
        &:hover{
          background-color: #ff7336;
        }
      }
    }
  }

  .footer-menu {
    text-transform: uppercase;

    li {
      margin-bottom: 13px;
      font-size: 14px;
      word-spacing: -1px;
    }
    li.margin-none{
      margin-bottom: 0;
    }
  }

  .footer-contact__item {
    position: relative;
    padding-left: 40px;
    line-height: 15px;
    vertical-align: middle;
    margin-bottom: 16px;

    i {
      position: absolute;
      left: -2px;
    }
  }
}

