.main__menu {
  margin-right: -15px;
  text-align: right;
  // width: 70%;
  nav {
    ul {
      font-size: 0;

      li {
        font-size: 18px;
        font-weight: 700;
        color: #666666;
        margin: 0 25px;
        position: relative;
        transition: all 0.5s ease;
        height: 100px;
        line-height: 100px;
        cursor: pointer;
        font-family: 'opensans', sans-serif;
        a {
          span {
            text-transform: capitalize;
            display: inline-block;
            letter-spacing: 1.1px;
          }
        }

        &:hover {
          color: #ea723d;
        }

        &:hover::after {
          width: 100%;
        }

        &::after {
          position: absolute;
          content: "";
          bottom: 0;
          left: 0;
          width: 0;
          height: 2px;
          background: #ea723d;
          transition: all 0.5s ease;
        }
      }
      li.padding-none{
        margin-right: 0;
      }
    }
  }
}

// home
.partner {
  padding-top: 65px;

  font-weight: 400;
  color: #666;
  position: relative;
  &::after{
    content: "";
