//feature
.feature {
  padding: 50px 0 120px;
  position: relative;

  &__slider {
    .feature-item {
      padding: 0 15px 40px;
    }
  }

  &__rooms {
    padding: 15px 0;
    border-bottom: 1px solid #eee;
    font-family: 'opensans', sans-serif;
    color: #666;

    .cross {
      position: relative;
    }

    .cross::after {
      content: '|';
      position: absolute;
      right: 0;
      top: 0;
      color: #eee;
    }
  }

  &__infor {
    padding: 15px 25px 25px 20px;

    &--time {
      font-weight: 700;
      font-family: 'opensans', sans-serif;
    }

    &--title {
      color: #222;
      font-size: 20px;
      margin: 13px 0 10px;
    }

    &--des { 
      color: #666;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;
      line-height: 25px;
      font-family: 'opensans', sans-serif; 
    }

    .sale {
      font-family: 'opensans', sans-serif;
      color: #222;
      margin: 20px 0 15px;
    }

    .price {
      font-size: 20px;
      font-weight: 400;
      color: #222;
    }

    .btn-view {
      position: absolute;
      right: 25px;
      bottom: 30px;
      color: #ea723d;
    }
  }

  .owl-nav {
    width: 100%;
    top: 38%;
    right: 0;
    margin-top: 0;

    .owl-prev{
      text-align: left;
    }
    .owl-next{
      text-align: right;
    }
    .owl-prev,
    .owl-next {
      width: 50%;
      position: relative;
      .feature-nav--next,
      .feature-nav--previous {
        opacity: 0.8;
        background-color: #999;
        width: 42px;
        height: 42px;
      }

      .feature-nav--next {
        // position: absolute;
        right: -65px;
      }

      .feature-nav--previous {
        position: absolute;
        left: -65px;
      }

      .nav--next:hover,
      .nav--previous:hover {
        background-color: #ea723d;
      }
    }
  }

  .btn-show-all,
  .btn-mini {
    margin: auto;
    p {
      font-weight: 600;
      font-size: 20px;
      color: #666;
      border-bottom: 1px solid #666666;
      cursor: pointer;
      transition: all 0.5s ease;
      display: initial;
    }

    p:hover {
      color: #ea723d;
      border-color: #ea723d;
    }
  }
  .btn-mini{
    margin-top: 30px;
  }
}
.feature::after {
  content: "";
  background-image: url('images/property.png');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  opacity: 0.03;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;   
}

