    transition: all 0.5s ease;
  }
  a:hover{
    background-color: #ea723d;
    color: #fff;
  }
  li:last-child >a{
    border:none;
  }
}

.mobile__wrap{
  position: relative;
  overflow-y: scroll;
  height: 100vh;
}

.mobile__exit{
  position: absolute;
  top: 0;
  right: -33px;
  button{
    color: #fff;
    background: #222;
    border: none;
    width: 33px;
    height: 33px;
    font-weight: 700;
    font-size: 1.5rem;
    opacity: 0.5;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
  }
  button:focus{
    outline:none;
  }
}
.mobile--bg{
  background-color: #222;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100vw;
  height: 100vh;
  z-index: -1;
  opacity: 0;
  visibility: hidden;
  transition: all 0.5s ease;
}
.mobile--bg.active{
  z-index: 1000;
  opacity: 0.6;
  visibility: visible;
}
