.infor_patner {
  &__form{
    padding: 31px 0 66px;
    .modal-header{
      border-bottom: 0px;
      h1{
        color: #222;
        font-weight: 600;
        font-family: 'poppins',sans-serif;
        font-size: 36px;
        line-height: 72px;
        margin-bottom: 11px;
      }
      h4{
        color: #8f8f8f;
        font-family: 'poppins',sans-serif;
        font-weight: 600;
        line-height: 42px;
        font-size: 20px;
      }
    }
    .form-infor_patner--item{
      position: relative;
      width: 58%;
      margin: auto;
      i{
        position: absolute;
        left: 0;
        width: 22px;
        height: 22px;
        font-size: 22px;
        bottom: 15px;
        color: #ccc;
      }
      input{
        font-size: 18px;
        padding-left: 30px;
        width: 100%;
        line-height: 60px;
        height: 60px;
        padding-top: 15px;
        border-bottom: 1px solid #eee;
        border-top: 0;
        border-left: 0;
        border-right: 0;
        &::placeholder{
          color: #ccc;
        }
        &.btn-send__patner{
          margin-top: 60px;
          text-transform: uppercase;
          color: #eee;
          padding: 0;
          border: none;
          font-weight: 600;
          font-family: 'poppins',sans-serif;
        }
      }
    }
  }
}
#infor_patner{
  .col-8{
    margin: auto;
  }
}
#modal-partner-thank{
  .modal-content{
    padding: 60px 0;
    .modal-header{
      padding: 0 150px;
      border-bottom: none;
      h3{
        font-size: 36px;
        line-height: 64px;
        letter-spacing: 1px;
        color: #222;
        font-weight: 600;
        word-spacing: 5px;
        font-family: 'poppins',sans-serif;
      }
    }
    .modal-body{
      padding: 0 150px;
      p.modal_thanks__desc{
        font-size: 20px;
        span{
          color: #ed8a63;
        }
      }
    }
  }
}