/*********************
    - 2. General -
*********************/

html,
body {
  margin: 0px;
  padding: 0px;
  font-size: 14px;
  font-weight: 400;
  font-family: 'poppins',sans-serif;
}

.list-inline {
  margin: 0px;
  padding: 0px;
  list-style: none;
}

.list-inline li {
  @include dis(middle);
}

a {
  color: inherit;
  text-decoration: none;
}
a:hover{
  text-decoration: none;
  color: inherit;
}

img {
  max-width: 100%;
  border: none;
  vertical-align: middle;
}

.fleft {
  float: left;
}

.fright {
  float: right;
}

.clear-fix,
.clearfix {
  clear: both;
}
p,h3,h2{
  margin: 0;
}

.text-right{
  text-align: right;
}

.menu--base{
  margin: 0;
  padding: 0;
  list-style: none;;
}
.item-none{
  height: 30px;
}

*:focus{
  outline: none;
}

.white{
  color: #bcbaba;
}
// button
.btn-base{
  background: #ea723d;
  color: #fff;
  border: none;
  border-radius: 10px;
  padding: 0;
}

.highlight{
  color: #f4773f;
}

.highlight2{
  color: #ea723d;
}

.text-left{
  text-align: left;
}

.text-center{
  text-align: center;
}

.active{
  display: block !important;
  z-index: 99;
}

.disabled{
  display: none;
}

.link-base{
  font-family: 'opensans', sans-serif;
  color: #f4773f;
  font-weight: 700;
}

.myslider{
  .owl-nav{
    position: absolute;
    margin-top: 0;
    .owl-prev:hover ,
    .owl-next:hover{
      background: none;
    }
    .owl-prev,
    .owl-next{
      margin: 0;
      border-radius: 50%;
      .nav--next,
      .nav--previous{
        height: 36px;
        width: 36px;
        display: inline-block;
        line-height: 36px;
        vertical-align: middle;
        border-radius: 50%;
        position: relative;
        transition: all 0.5 ease;
        img{
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          margin: auto;
        }
      }
      .nav--next:hover,
      .nav--previous:hover{
        background-color: #ea723d;
      }
    }
    .owl-prev:focus ,
    .owl-next:focus{
      outline: none;
    }
  }
}
.col-50{
  width: 50%;
  padding: 0 15px;
}
.btn-primary.focus, .btn-primary:focus{
  box-shadow: none;
}
label.error{
  font-size: 9px;
    color: red !important;
    position: absolute;
    z-index: 9;
    width: 100%;
    text-align: left;
    left: 0;
    bottom: -20px;
}
.form-login--item label.error{
  bottom: -10px;
  font-size: 12px;
}